.card {
  $self: &;
  position: relative;
  &--wait {
    #{$self}__header {
      background-color: var(--color-wait-l);
    }
  }
  &--timeout {
    #{$self}__header {
      background-color: var(--color-timeout);
    }
  }
  &--process {
    #{$self}__header {
      background-color: var(--color-process-l);
    }
  }
  &--upcoming {
    #{$self}__header {
      background-color: var(--color-upcoming-l);
    }
    &:first-child {
      margin-top: 2rem;
    }
  }
  &--overdue {
    #{$self}__header {
      background-color: var(--color-overdue-l);
    }
    &:first-child {
      margin-top: 2rem;
    }
  }
  &__header {
    border-radius: 1rem 1rem 0 0;
    padding: 2rem 1.2rem 1rem 1.6rem;
    display: flex;
  }
  &__info {
    font-size: 0;
    margin-bottom: 0.4rem;
  }
  &__avatar {
    width: 6rem;
    overflow: hidden;
    margin-right: 1rem;
    img {
      border-radius: 50%;
      max-width: 100%;
      display: block;
      margin: 0;
    }
  }
  &__name {
    font-size: 2.2rem;
    color: #000;
    display: inline-block;
    line-height: 3.2rem;
    vertical-align: middle;
    margin-right: 1rem;
  }
  &__course-name {
    color: #000;
    font-size: 1.8rem;
    line-height: 1.6;
  }
  &__course-number {
    color: #000;
    font-size: 1.8rem;
    line-height: 1.6;
  }
  &__location {
    color: #000;
    font-size: 1.6rem;
    line-height: 1.6;
  }
  &__status {
    background: var(--color-status-bg);
    padding: 1.4rem 2rem;
    display: grid;
    grid-template-columns: 5rem auto;
    gap: 2rem;
  }
  &__status-detail {
    font-size: 0;
    span {
      display: inline-block;
      width: 50%;
      font-size: 1.6rem;
      color: #000;
      line-height: 1.6;
    }
  }
  &__footer {
    background-color: var(--color-card-footer);
    padding: 1.4rem 2rem;
    font-size: 0;
    border-radius: 0 0 1rem 1rem;
  }
  &__btn {
    @extend .site-btn;
  }
  &__process {
    padding: 1.4rem 2rem;
    &--now {
      background-color: var(--color-card-process-now-bg);
    }
    &--next {
      background-color: var(--color-card-process-next-bg);
      border-radius: 0 0 1rem 1rem;
    }
  }
  &__process-row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.4rem;
  }
  &__process-title,
  &__process-course,
  &__process-time {
    font-size: 1.6rem;
    line-height: 2.2rem;
    color: #000;
    font-weight: 500;
  }
  &__process-title {
    text-transform: uppercase;
    margin-right: 1rem;
    font-weight: bolder;
  }
  &__process-course {
    margin-right: auto;
  }
}

.status-square {
  flex-basis: 5rem;
  width: 5rem;
  height: 5rem;
  border-radius: 1.2rem;
  box-sizing: border-box;
  &--a {
    background-color: #eb2860;
  }
  &--b {
    background-color: #ff7826;
  }
  &--c {
    background-color: #febc01;
  }
  &--d {
    background: transparent url(../images/status-d.jpg) no-repeat 0 0;
    background-size: 5rem;
    border: solid 0.1rem #000;
  }
}

.identity {
  text-align: center;
  box-sizing: border-box;
  min-width: 8.8rem;
  vertical-align: middle;
  height: 3.2rem;
  display: inline-block;
  line-height: 3.2rem;
  border-radius: 3.2rem;
  padding: 0 2rem;
  color: var(--color-identity-text);
  font-size: 1.6rem;
  &--consultant {
    background-color: var(--color-consultant);
  }
  &--nurse {
    background-color: var(--color-nurse);
  }
  &--beautician {
    background-color: var(--color-beautician);
  }
  &--control {
    background-color: var(--color-control);
  }
  &--doctors {
    background-color: var(--color-doctors);
  }
}

.card-option {
  position: absolute;
  right: -1.6rem;
  top: 5.5rem;
  width: 9.2rem;
  z-index: 50;
  opacity: 0;
  pointer-events: none;
  transition: all 300ms ease;
  &.is-active {
    opacity: 1;
    pointer-events: auto;
  }
  &__btn {
    width: 9.2rem;
    border: solid 0.1rem var(--color-card-option);
    border-bottom: 0;
    background-color: #fff;
    padding: 0;
    line-height: 3rem;
    height: 3.2rem;
    box-sizing: border-box;
    font-size: 1.4rem;
    color: var(--color-grey-d);
    position: relative;
    &:last-child {
      border-bottom: solid 0.1rem var(--color-card-option);
    }
    &:hover {
      background-color: #d7d7d7;
    }
    &:active {
      background-color: #d7d7d7 ;
    }
  }
}

.card-reservation {
  border-radius: 1rem;
  background-color: var(--color-card-reservation);
  margin-bottom: 2rem;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  position: relative;
  &__avatar {
    width: 6rem;
    overflow: hidden;
    margin-right: 1rem;
    img {
      border-radius: 50%;
      max-width: 100%;
      display: block;
      margin: 0;
    }
  }
  &__info {
    margin-right: 1rem;
  }
  &__row {
    display: flex;
    align-items: center;
  }
  &__name {
    font-size: 2.2rem;
    color: #000;
    display: inline-block;
    line-height: 3.2rem;
    vertical-align: middle;
    margin-right: 1rem;
  }
  &__course-name {
    color: #000;
    font-size: 1.8rem;
    line-height: 1.6;
  }
  &__course-number {
    color: #000;
    font-size: 2.2rem;
    line-height: 3.2rem;
  }
  &__date,
  &__time {
    color: #000;
    font-size: 1.8rem;
    line-height: 3.2rem;
  }
  &__time {
    margin-right: 3rem;
  }
}