.work-detail {
  height: 100vh;
  background-color: #dadada;
  overflow-y: scroll;
  box-sizing: border-box;
  &__date-picker {
    padding-top: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 3rem;
    & > input[type=date] {
      width: 26rem;
      height: 6.2rem;
      border: 0;
      padding: 0 2rem;
      font-size: 2rem;
      color: #000;
      margin: 0 2rem;
      border-radius: 8rem;
    }
  }
  &__total {
    text-align: center;
    font-size: 3rem;
    color: #000;
    position: relative;
    padding-bottom: 3rem;
    &::after {
      width: calc(100% - 10rem);
      height: 0.1rem;
      background-color: #888888;
      position: absolute;
      left: 5rem;
      bottom: 0;
      content: "";
    }
  }
  &__ranking {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    padding: 5rem;
    position: relative;
    margin-bottom: 5rem;
    &::after {
      width: calc(100% - 10rem);
      height: 0.1rem;
      background-color: #888888;
      position: absolute;
      left: 5rem;
      bottom: 0;
      content: "";
    }
  }
  &__group {
    h3 {
      color: #1b97c8;
      font-size: 2.4rem;
      text-align: center;
      margin-bottom: 2rem;
    }
  }
  &__ranking-table {
    width: 30rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2rem;
    td {
      line-height: 6rem;
      font-size: 2rem;
      color: #000;
      padding: 0 1rem;
      text-align: center;
    }
  }
  &__daily-work-table {
    width: 30rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2rem;
    th,
    td {
      line-height: 6rem;
      font-size: 2rem;
      color: #000;
      padding: 0 1rem;
      text-align: center;
    }
  }
  &__daily-work {
    padding-bottom: 4rem;
  }
  &__wrap {
    padding: 5rem 0;
    h3 {
      font-size: 2.4rem;
      margin-bottom: 2rem;
      color: #1b97c8;
      padding-left: 5rem;
    }
  }
  &__list {
    padding-bottom: 4rem;
    position: relative;
    &--empty {
      min-height: 40rem;
    }
  }
  &__work-item {
    padding: 4rem 6.4rem;
    &:nth-child(odd) {
      background-color: #bbbbbb;
    }
    &:nth-child(even) {
      background-color: #d8d8d8;
    }
  }
  &__row {
    display: flex;
  }
  &__date,
  &__status {
    font-size: 2rem;
    color: #000;
    line-height: 1.6;
    margin-right: 2rem;
  }
  &__status {
    &.is-complete {
      color: #417505;
    }
    &.is-timeout {
      color: #d0021b;
    }
    &.is-undone {
      color: #00679d;
    }
  }
  &__reservation {
    font-size: 2rem;
    line-height: 1.6;
    color: #808080;
  }
  &__client-name,
  &__doctor-name,
  &__start,
  &__end,
  &__time-consuming {
    font-size: 2rem;
    line-height: 1.6;
    color: #000;
    margin-right: 2rem;
  }
  &__empty {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%,-50%,0);
    img {
      width: 20rem;
      display: block;
      margin: 0 auto 2rem
    }
    p {
      font-size:  1.8rem;
      color: #888888;
      text-align: center;
    }
  }
}