.reservation-search {
  width: 50rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%,-50%,0);
  border-radius: 1rem;
  overflow: hidden;
  pointer-events: none;
  opacity: 0;
  transition: all 300ms ease;
  &.is-active {
    pointer-events: auto;
    opacity: 1;
  }
  &__header {
    @extend .pop-ui-header;
  }
  &__btn-close {
    @extend .pop-ui-btn-close;
  }
  &__content {
    background-color: var(--color-reservation-search-content);
    padding: 4.6rem;
    h3 {
      font-size: 2.4rem;
      color: var(--color-grey-d);
      margin-bottom: 3rem;
      font-weight: 500;
    }
  }
  &__row {
    margin-bottom: 1.6rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &:first-child {
      margin-bottom: 7.6rem;
    }
    &:last-child {
      margin-bottom: 0;
      margin-top: 4.2rem;
    }
    label {
      display: inline-block;
      width: 8rem;
      font-size: 2rem;
      color: var(--color-grey-d);
    }
    input {
      box-sizing: border-box;
      width: 30rem;
      height: 4.6rem;
      line-height: 4.6rem;
      font-size: 2rem;
      color: var(--color-grey-d);
      border-radius: 4.6rem;
      padding: 0 2rem;
      border: 0;
    }
  }
  &__btn-search {
    background-color: var(--color-reservation);
    width: 25rem;
    display: block;
    margin: 0 auto;
    font-size: 1.8rem;
    color: #fff;
    text-align: center;
    height: 5rem;
    border-radius: 5rem;
    border: 0;
  }
}

.btn-reservation,
.btn-new {
  z-index: 500;
  width: 9.6rem;
  height: 9.6rem;
  background-color: var(--color-reservation);
  border-radius: 50%;
  position: absolute;
  bottom: 4.8rem;
  right: 4.8rem;
  border: 0;
  transition: all 300ms ease;
  &::before,
  &::after {
    content: "";
    width: 4.8rem;
    height: 0.8rem;
    background-color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%,-50%,0);
  }
  &::after {
    transform: translate3d(-50%,-50%,0) rotate(90deg);
  }
  &.is-hide {
    opacity: 0;
    pointer-events: none;
  }
}

.reservation-result {
  width: 58rem;
  padding: 3rem 0;
  position: absolute;
  top: 8rem;
  pointer-events: none;
  opacity: 0;
  left: 50%;
  transform: translate3d(-50%,0,0);
  &.is-active {
    pointer-events: none;
    opacity: 1;
  }
  &__search {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 2rem;
  }
  &__content {
    height: 68rem;
    overflow-y: auto;
    overflow-x: hidden;
    position: relative;
  }
  &__empty {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%,-50%,0);
    img {
      width: 12rem;
      display: block;
      margin: 0 auto 2rem
    }
    p {
      font-size:  1.8rem;
      color: #fff;
      text-align: center;
    }
  }
}

.reservation-client {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 58rem;
  opacity: 0;
  pointer-events: none;
  transition: all 300ms ease;
  border-radius: 1rem;
  background-color: var(--color-reservation-client);
  height: 80rem;
  transform: translate3d(-50%,-38.2rem,0);
  padding: 4rem;
  box-sizing: border-box;
  &.is-active {
    opacity: 1;
    pointer-events: auto;
  }
  &__content {
    margin-bottom: 4rem;
    &:last-child {
      margin-bottom: 0;
    }
    h3 {
      color: #fff;
      font-size: 2.4rem;
      font-weight: 500;
      margin-bottom: 2rem;
    }
  }
  &__client-info {
    background-color: var(--color-status-bg);
    border-radius: 1rem;
    overflow: hidden;
  }
  &__client-selected {
    height: 6.4rem;
    width: 25rem;
    background-color: #fff;
    border-radius: 6.4rem;
    padding: 1rem;
    box-sizing: border-box;
    margin-left: 0.6rem;
    display: flex;
    align-items: center;
  }
  &__avatar {
    width: 4.8rem;
    overflow: hidden;
    margin-right: 1rem;
    img {
      border-radius: 50%;
      max-width: 100%;
      display: block;
      margin: 0;
    }
  }
  &__name {
    font-size: 3rem;
    color: #000;
  }
  &__mobile {
    font-size: 1.6rem;
  }
  &__row {
    height: 11rem;
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      font-size: 2.2rem;
      color: #000;
    }
  }
  &__btn-client {
    background-color: var(--color-reservation-client-btn);
    font-size: 2rem;
    height: 5.6rem;
    line-height: 5.6rem;
    text-align: center;
    width: 100%;
    border: 0;
    color: var(--color-grey-d);
    position: relative;
    &::after {
      width: 100%;
      height: 100%;
      background: var(--color-user-login-bg) url(../images/icon-check.svg) no-repeat center center;
      background-size: 4rem auto;
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;
      content: "";
    }
    &.is-complete {
      &::after {
        opacity: 1;
      }
    }
  }
  &__item {
    background-color: var(--color-status-bg);
    border-radius: 1rem;
    overflow: hidden;
    margin-bottom: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    height: 9rem;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__step {
    font-size: 8rem;
    color: #000;
    opacity: 0.18;
    padding: 0 2rem;
    line-height: 9rem;
  }
  &__detail {
    margin-right: auto;
    padding-top: 1.6rem;
  }
  &__type {
    font-size: 2.2rem;
    color: #000;
    line-height: 1.6;
  }
  &__selected {
    color: var(--color-user-login-bg);
    font-size: 1.8rem;
  }
  &__btn-select {
    width: 12rem;
    background-color: var(--color-reservation-client-btn);
    font-size: 2rem;
    color: var(--color-grey-d);
    padding: 0;
    border: 0;
    height: 9rem;
    position: relative;
    &::after {
      width: 100%;
      height: 100%;
      background: var(--color-user-login-bg) url(../images/icon-check.svg) no-repeat center center;
      background-size: 4rem auto;
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;
      content: "";
    }
    &.is-complete {
      &::after {
        opacity: 1;
      }
    }
  }
}

.create-client-data {
  position: absolute;
  z-index: 100;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  pointer-events: none;
  transition: all 300ms ease;
  &.is-active {
    opacity: 1;
    pointer-events: auto;
  }
  &__overlay {
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__inner {
    width: 50rem;
    border-radius: 1rem;
    overflow: hidden;
  }
  &__header {
    @extend .pop-ui-header;
  }
  &__btn-close {
    @extend .pop-ui-btn-close;
  }
  &__content {
    background-color: var(--color-reservation-search-content);
    padding: 3.2rem;
    h3 {
      font-size: 2.4rem;
      color: var(--color-grey-d);
      margin-bottom: 3rem;
      font-weight: 500;
    }
  }
  &__search {
    margin-bottom: 4rem;
  }
  &__row {
    margin-bottom: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    label {
      display: inline-block;
      width: 12rem;
      font-size: 2rem;
      color: var(--color-grey-d);
    }
    input {
      box-sizing: border-box;
      width: 30rem;
      height: 4.6rem;
      line-height: 4.6rem;
      font-size: 2rem;
      color: var(--color-grey-d);
      border-radius: 4.6rem;
      padding: 0 2rem;
      border: 0;
    }
  }
  &__select-wrap {
    width: 30rem;
    height: 4.6rem;
    line-height: 4.6rem;
    padding-right: 0;
    border-radius: 4.6rem;
    overflow: hidden;
    position: relative;
    &::after {
      width: 2.4rem;
      height: 4.6rem;
      top: 0;
      right: 2rem;
      background: transparent url(../images/icon-arrow.svg) no-repeat center center;
      background-size: 2.4rem auto;
      content: "";
      position: absolute;
      pointer-events: none;
    }
    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      text-indent: 1px;
      text-overflow: '';
      border: 0;
      font-size: 2rem;
      color: var(--color-grey-d);
      border: 0;
      width: 100%;
      height: 100%;
      padding: 0 2rem;
      box-sizing: border-box;
    }
  }
  &__btn-search,
  &__btn-save {
    background-color: var(--color-reservation);
    width: 25rem;
    display: block;
    margin: 0 auto;
    font-size: 1.8rem;
    color: #fff;
    text-align: center;
    height: 5rem;
    border-radius: 5rem;
    border: 0;
  }
}

