.reservation-time {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 58rem;
  opacity: 0;
  pointer-events: none;
  transition: all 300ms ease;
  border-radius: 1rem;
  background-color: var(--color-reservation-client);
  height: 86rem;
  transform: translate3d(-50%,-38.2rem,0);
  padding: 4rem;
  box-sizing: border-box;
  &.is-active {
    opacity: 1;
    pointer-events: auto;
  }
  &__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 2rem;
    h3 {
      color: #fff;
      font-size: 2.4rem;
      font-weight: 500;
    }
  }
  &__wrap {
    background-color: var(--color-reservation-search-content);
    border-radius: 1rem;
    padding: 2rem 4rem;
    height: 62rem;
    overflow-y: auto;
    overflow-x: hidden;
    margin-bottom: 3rem;
  }
  &__date {
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
  }
  &__btn-prev,
  &__btn-next {
    width: 3.6rem;
    height: 3.6rem;
    border: 0;
    background-color: #fff;
    border-radius: 0.6rem;
    position: relative;
    margin: 0 0.6rem;
    &::before {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      border-top: solid 1rem transparent;
      border-right: solid 1.6rem #000;
      border-bottom: solid 1rem transparent;;
      border-left: solid 1rem transparent;;
      left: 50%;
      top: 50%;
      transform: translate3d(-75%,-50%,0);
    }
  }
  &__btn-next {
    &::before {
      transform: translate3d(-25%,-50%,0) rotate(180deg);
    }
  }
  &__current-date {
    font-size: 2rem;
    color: #000;
    padding: 0 1.6rem;
  }
  &__btn-select-date {
    @extend .site-btn-lg;
  }
  &__day-wrap {
    display: flex;
    justify-content: space-between;
  }
  &__days {
    text-align: center;
  }
  &__week {
    font-size: 2rem;
    color: var(--color-week);
    line-height: 1.6;
  }
  &__day {
    font-size: 2rem;
    color: #000;
    line-height: 1.6;
  }
  &__period {
    font-size: 1.3rem;
    line-height: 1.6;
    margin-bottom: 0.6rem;
    color: #000;
    border: solid 0.1rem transparent;
    box-sizing: border-box;
    padding: 0 1rem;
    &.is-disable {
      pointer-events: none;
      color: var(--color-period-disable);
    }
    &.is-active {
      border-color: var(--color-user-login-bg);
      color: var(--color-user-login-bg);
    }
  }
  &__btn-confirm {
    background-color: var(--color-reservation);
    width: 25rem;
    display: block;
    margin: 0 auto;
    font-size: 1.8rem;
    color: #fff;
    text-align: center;
    height: 5rem;
    border-radius: 5rem;
    border: 0;
  }
}

