.service-course-add {
  $self: &;
  height: 100vh;
  background-color: #dadada;
  overflow-y: scroll;
  &__basic-info {
    padding-bottom: 5rem;
    position: relative;
    &::after {
      width: calc(100% - 10rem);
      height: 0.1rem;
      background-color: #888888;
      position: absolute;
      left: 5rem;
      bottom: 0;
      content: "";
    }
  }
  &__basic-info-inner {
    display: flex;
  }
  &__col {
    flex-basis: 100%;
    padding: 5rem;
    position: relative;
    h3 {
      color: #1b97c8;
      font-size: 2.4rem;
      margin-bottom: 3rem;
    }
  }
  &__row {
    display: flex;
    margin-bottom: 1.6rem;
    & > label {
      width: 16rem;
      font-size: 2rem;
      color: #000;
      line-height: 4.5rem;
    }
    & > input {
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      width: 29rem;
      box-sizing: border-box;
      color: #000;
      font-size: 2rem;
      line-height: 4.5rem;
      height: 4.5rem;
      padding: 0 1rem;
    }
  }
  &__select-wrap {
    width: 29rem;
    height: 4.5rem;
    line-height: 4.5rem;
    padding-right: 0;
    border-radius: 0.5rem;
    overflow: hidden;
    position: relative;
    &::after {
      top: 50%;
      transform: translate3d(0,-50%,0);
      right: 2rem;
      width: 0; 
      height: 0; 
      border-left: 0.8rem solid transparent;
      border-right: 0.8rem solid transparent;
      border-top: 0.8rem solid #000;
      content: "";
      position: absolute;
      pointer-events: none;
    }
    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      text-indent: 1px;
      text-overflow: '';
      border: 0;
      font-size: 2rem;
      color: #000;
      border: 0;
      width: 100%;
      height: 100%;
      padding: 0 2rem;
      box-sizing: border-box;
    }
  }
  &__flow {
    padding: 5rem;
    position: relative;
    &::after {
      width: calc(100% - 10rem);
      height: 0.1rem;
      background-color: #888888;
      position: absolute;
      left: 5rem;
      bottom: 0;
      content: "";
    }
    &:last-child {
      &::after {
        display: none;
      }
    }
    h3 {
      color: #1b97c8;
      font-size: 2.4rem;
      margin-bottom: 3rem;
    }
  }
  &__flow-wrap {
    margin-bottom: 4rem;
  }
  &__flow-item {
    margin-bottom: 6rem;
    display: flex;
    &:last-child {
      margin-bottom: 0;
    }
    #{$self}__select-wrap {
      width: 22rem;
      margin-right: 3rem;
    }
    & > input {
      width: 14rem;
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      box-sizing: border-box;
      color: #000;
      font-size: 2rem;
      line-height: 4.5rem;
      height: 4.5rem;
      padding: 0 1rem;
      margin: 0 1rem;
    }
  }
  &__step {
    flex-basis: 12rem;
    font-size: 2rem;
    color: #000;
    line-height: 4.5rem;
  }
  &__time-wrap {
    display: flex;
    width: 29rem;
    margin-right: 2rem;
    & > input {
      min-width: 3rem;
      flex: 1;
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      box-sizing: border-box;
      color: #000;
      font-size: 2rem;
      line-height: 4.5rem;
      height: 4.5rem;
      padding: 0 1rem;
      margin: 0 0.4rem;
    }
  }
}

.service-course-setting {
  z-index: 300;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  pointer-events: none;
  transition: all 300ms ease;
  &.is-active {
    opacity: 1;
    pointer-events: auto;
  }
  &__overlay {
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__inner {
    width: 96rem;
    background-color: #d8d8d8;
  }
  &__header {
    background-color: #989898;
    height: 4.8rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    h3 {
      font-size: 2rem;
      color: #fff;
    }
  }
  &__btn-close {
    width: 1.8rem;
    background-color: transparent;
    height: 1.8rem;
    position: relative;
    border: 0;
    &::before,
    &::after {
      width: 2rem;
      height: 0.2rem;
      background-color: #000;
      position: absolute;
      content: "";
      left: 50%;
      top: 50%;
    }
    &::before {
      transform: translate3d(-50%,-50%,0) rotate(45deg);
    }
    &::after {
      transform: translate3d(-50%,-50%,0) rotate(-45deg);
    }
  }
  &__content {
    padding: 3.6rem;
    h4 {
      font-size: 2.4rem;
      color: #1b97c8;
      margin-bottom: 2rem;
    }
  }
  &__row {
    display: flex;
    margin-bottom: 1.6rem;
    & > label {
      width: 12rem;
      font-size: 2rem;
      color: #000;
      line-height: 4.5rem;
    }
    & > input {
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      width: 29rem;
      box-sizing: border-box;
      color: #000;
      font-size: 2rem;
      line-height: 4.5rem;
      height: 4.5rem;
      padding: 0 1rem;
    }
  }
  &__select-wrap {
    width: 29rem;
    height: 4.5rem;
    line-height: 4.5rem;
    padding-right: 0;
    border-radius: 0.5rem;
    overflow: hidden;
    position: relative;
    &::after {
      top: 50%;
      transform: translate3d(0,-50%,0);
      right: 2rem;
      width: 0; 
      height: 0; 
      border-left: 0.8rem solid transparent;
      border-right: 0.8rem solid transparent;
      border-top: 0.8rem solid #000;
      content: "";
      position: absolute;
      pointer-events: none;
    }
    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      text-indent: 1px;
      text-overflow: '';
      border: 0;
      font-size: 2rem;
      color: #000;
      border: 0;
      width: 100%;
      height: 100%;
      padding: 0 2rem;
      box-sizing: border-box;
    }
  }
  &__skill-wrap {
    padding-top: 0.7rem;
    display: flex;
    max-width: 71rem;
    flex-wrap: wrap;
  }
  &__skill-item {
    display: flex;
    margin-right: 2rem;
    margin-bottom: 2rem;
    & > input[type=checkbox] {
      width: 3rem;
      height: 3rem;
      border: solid 0.1rem #979797;
      background-color: #fff;
      padding: 0;
      margin-right: 0.8rem;
    }
    & > label {
      line-height: 3rem;
      height: 3rem;
      font-size: 2rem;
      color: #000;
    }
  }
}