.daily-report {
  padding-top: 15rem;
  &__header {
    background-color: var(--color-daily-report);
    box-sizing: border-box;
    height: 8rem;
    padding: 0 3rem;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 100;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  &__info {
    display: flex;
    align-items: center;
    h2 {
      font-size: 3rem;
      color: #fff;
      font-weight: 500;
    }
  }
  &__doctor-name {
    font-size: 3rem;
    color: #fff;
    margin-left: 1rem;
    padding-left: 1rem;
    border-left: solid 0.2rem #fff;
    margin-right: 1rem;
  }
  &__btn-select-doctor {
    @extend .site-btn-lg;
  }
  &__date {
    display: flex;
    align-items: center;
  }
  &__btn-prev,
  &__btn-next {
    width: 3.6rem;
    height: 3.6rem;
    border: 0;
    background-color: #fff;
    border-radius: 0.6rem;
    position: relative;
    margin: 0 0.6rem;
    &::before {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      border-top: solid 1rem transparent;
      border-right: solid 1.6rem #000;
      border-bottom: solid 1rem transparent;;
      border-left: solid 1rem transparent;;
      left: 50%;
      top: 50%;
      transform: translate3d(-75%,-50%,0);
    }
  }
  &__btn-next {
    &::before {
      transform: translate3d(-25%,-50%,0) rotate(180deg);
    }
  }
  &__current-date {
    font-size: 3rem;
    color: #fff;
    padding: 0 1.6rem;
  }
  &__btn-select-date {
    @extend .site-btn-lg;
  }
  &__outpatient-wrap {
    background-color: var(--color-daily-report-outpatient);
    height: 7.5rem;
    display: flex;
    align-items: center;
    padding-left: 13rem;
    box-sizing: border-box;
    position: fixed;
    left: 0;
    width: 100%;
    top: 8rem;
    z-index: 10;
    span {
      font-size: 2.4rem;
      color: #fff;
      flex: 1;
      text-align: center;
    }
  }
  &__calendar {
    position: relative;
    display: flex;
    padding-left: 13rem;
    padding-top: 2rem;
    background-color: var(--color-daily-report-outpatient-bg);
  }
  &__time {
    position: absolute;
    left: 0;
    top: 2rem;
    background-color: #E5E5F7;
    background-image: linear-gradient(0deg, #d9d9d9 50%, #fff 50%);
    background-size: 13rem 48rem;
    div {
      box-sizing: border-box;
      width: 13rem;
      height: 8rem;
      position: relative;
      strong {
        font-weight: 500;
        position: absolute;
        font-size: 2.4rem;
        color: #000;
        left: 50%;
        top: 0;
        transform: translate3d(-50%,-50%,0);
      }
    }
  }
  &__outpatient {
    flex: 1;
  }
  &__period {
    box-shadow: inset 0 0 0.1rem 0.05rem #fff;
    height: 8rem;
    &:last-child {
      opacity: 0.5;
    }
  }
}

.period {
  display: flex;
  background-color: #fff;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 1.4rem 2rem;
  &__img {
    width: 3.6rem;
    margin-right: 1.6rem;
    padding-top: 0.6rem;
    img {
      width: 3.6rem;
      height: 3.6rem;
      border-radius: 50%;
    }
  }
  &__row {
    margin-bottom: 0.6rem;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__name,
  &__date,
  &__mobile,
  &__course {
    font-size: 1.3rem;
    color: #000;
  }
  &__name {
    margin-right: 1rem;
  }
}