.wide-screen {
  display: grid;
  grid-template-columns: 110rem 234rem;
  height: 100%;
  overflow-y: hidden;
  overflow-x: auto;
}

.normal-screen {
  display: grid;
  grid-template-columns: 40rem 40rem 1fr;
  height: 100%;
  overflow-y: hidden;
  overflow-x: auto;
  &.is-pad {
    grid-template-columns: 1fr;
  }
}

.panel {
  $self: &;
  position: relative;
  width: 100%;
  &--wait {
    background-color: var(--color-wait-bg);
    #{$self}__header {
      background-color: var(--color-wait);
    }
    #{$self}__main {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
    }
  }
  &--process {
    background-color: var(--color-process-bg);
    #{$self}__header {
      background-color: var(--color-process);
    }
    #{$self}__main {
      display: grid;
      grid-template-columns: repeat(5, 1fr);
    }
  }
  &--upcoming {
    background-color: var(--color-wait-bg);
    #{$self}__header {
      background-color: var(--color-upcoming-l);
    }
    #{$self}__main {
      display: grid;
      grid-template-columns: 1fr;
    }
  }
  &--overdue {
    background-color: var(--color-overdue-bg);
    #{$self}__header {
      background-color: var(--color-overdue);
      h2 {
        color: #fff;
      }
    }
    #{$self}__main {
      display: grid;
      grid-template-columns: 1fr;
    }
  }
  &--reservation {
    background-color: var(--color-wait-bg);
    #{$self}__header {
      background-color: var(--color-reservation);
      h2 {
        color: #fff;
      }
    }
    #{$self}__main {
      display: flex;
      justify-content: center;
      align-items: flex-start;
    }
  }
  &__header {
    box-sizing: border-box;
    height: 8rem;
    padding: 0 3rem;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 100;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    h2 {
      font-weight: 500;
      color: var(--color-grey-d);
      line-height: 8rem;
      font-size: 3rem;
    }
  }
  &__control {
    display: flex;
    align-items: center;
  }
  &__account {
    margin-right: 2rem;
    width: 15.6rem;
    height: 4rem;
    border-radius: 4rem;
    background-color: var(--color-control);
    display: flex;
    box-sizing: border-box;
    padding: 0.5rem 0.6rem;
  }
  &__account-avatar {
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    margin-right: 1.2rem;
  }
  &__account-name {
    font-size: 2rem;
    color: var(--color-identity-text);
    line-height: 3rem;
  }
  &__btn-toggle {
    width: 3.6rem;
    height: 3.6rem;
    position: relative;
    background-color: transparent;
    border: 0;
    padding: 0;
    &::before,
    &::after {
      width: 3.6rem;
      height: 0.6rem;
      background-color: #fff;
      top: 50%;
      left: 50%;
      content: "";
      position: absolute;
    }
    &::before {
      transform: translate3d(-50%,-300%,0);
    }
    &::after {
      transform: translate3d(-50%,200%,0);
    }
    span {
      width: 3.6rem;
      height: 0.6rem;
      background-color: #fff;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%,-50%,0);
    }
  }
  &__main {
    box-sizing: border-box;
    overflow-y: auto;
    padding: 8rem 3rem 0 3rem;
    gap: 2rem;
    height: calc(100vh - 2rem);
    &--card {
      padding-top: 10rem;
    }
    &--empty {
      padding-top: 10rem;
      position: relative;
      &::after {
        content: "目前沒有任務";
        width: 100%;
        height: 100%;
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 2.4rem;
        color: #fff;
      }
    }
  }
}
