.site-btn {
  height: 3.2rem;
  background-color: #fff;
  color: var(--color-grey-d);
  font-size: 1.4rem;
  line-height: 3.2rem;
  padding: 0 1.6rem;
  border-radius: 3.2rem;
  border: 0;
  transition: all 300ms ease;
  margin-right: 0.8rem;
  &:hover {
    background-color: var(--color-card-btn-bg);
  }
  &:active {
    background-color: var(--color-card-btn-bg);
  }
}

.admin-btn {
  box-sizing: border-box;
  text-align: center;
  width: 9.2rem;
  display: inline-block;
  height: 3.2rem;
  background-color: var(--color-user-login-bg);
  color: #fff;
  font-size: 1.4rem;
  line-height: 3.2rem;
  padding: 0 1.6rem;
  border-radius: 3.2rem;
  border: 0;
  transition: all 300ms ease;
  margin: 0 0.6rem
}

.admin-btn-lg {
  box-sizing: border-box;
  text-align: center;
  width: 25rem;
  display: block;
  height: 5rem;
  background-color: #00679d;
  color: #fff;
  font-size: 1.8rem;
  line-height: 5rem;
  padding: 0 1.6rem;
  border-radius: 5rem;
  border: 0;
  transition: all 300ms ease;
  margin: 0 auto;
}

.site-btn-lg {
  height: 4rem;
  background-color: #fff;
  color: var(--color-grey-d);
  font-size: 1.8rem;
  line-height: 4rem;
  padding: 0 2.6rem;
  border-radius: 4rem;
  border: 0;
  transition: all 300ms ease;
  margin-right: 0.8rem;
  &:hover {
    background-color: var(--color-card-btn-bg);
  }
  &:active {
    background-color: var(--color-card-btn-bg);
  }
}

.pop-ui-header {
  background-color: var(--color-reservation-search-hd);
  padding: 0 2rem;
  height: 4.6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  h2 {
    font-size: 2rem;
    color: #fff;
    font-weight: 500;
  }
}

.pop-ui-btn-close {
  width: 1.8rem;
  background-color: transparent;
  height: 1.8rem;
  position: relative;
  border: 0;
  &::before,
  &::after {
    width: 2rem;
    height: 0.2rem;
    background-color: #fff;
    position: absolute;
    content: "";
    left: 50%;
    top: 50%;
  }
  &::before {
    transform: translate3d(-50%,-50%,0) rotate(45deg);
  }
  &::after {
    transform: translate3d(-50%,-50%,0) rotate(-45deg);
  }
}