.admin-pagination {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  &__btn-prev,
  &__btn-next {
    font-size: 2rem;
    color: #000;
    display: inline-block;
    border: 0;
    padding: 0;
    background-color: transparent;
    margin: 0 2rem;
    &.is-disable {
      color: #a2a2a2;
      pointer-events: none;
    }
  }
  &__current {
    font-size: 1.4rem;
    color: #fff;
    padding: 1.2rem 2rem;
    background-color: #1b97c8;
    border-radius: 1rem;
    margin-right: 1rem;
  }
  &__page {
    font-size: 2rem;
    color: #000;
  }
}

.admin-table {
  width: 100%;
  thead {
    cursor: default;
  }
  tr {
    background-color: #ebebeb;
    &:nth-child(even) {
      background-color: #fafafa;
    }
  }
  th {
    height: 7rem;
    background-color: #989696;
    font-size: 2rem;
    color: #fff;
    font-weight: 500;
    vertical-align: middle;
    &:last-child {
      span {
        display: none;
      }
      &::after {
        display: none;
      }
    }
  }
  td {
    text-align: center;
    height: 7rem;
    font-size: 2rem;
    color: #000;
    font-weight: 500;
    vertical-align: middle;
    &:last-child {
      padding-right: 6rem;
      text-align: right;
    }
    a {
      &:last-child {
        margin-right: 0;
      }
    }
  }
}
table.sortable th:not(.sorttable_sorted):not(.sorttable_sorted_reverse):not(.sorttable_nosort):after { 
  content: " \25BE" 
}